@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(26, 26, 26);
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
	text-align: center;
}

.title {
	font-weight: 700;
}

main {
	display: flex;
	flex-direction: row;
}

.monitor {
	margin-left: 2vw;
}

label {
	margin-right: .4em;
}

.buttons {
	display: flex;
	flex-direction: row;
	justify-content: center;
	margin-bottom: 1rem;
	grid-gap: .5rem;
	gap: .5rem;
}

button {
    display: inline-block;
    border: none;
	border-radius: .5rem;
    padding: .2rem;
    margin: 0;
    text-decoration: none;
    background: #424242;
    color: #ffffff;
    font-family: sans-serif;
    font-size: 1rem;
    cursor: pointer;
    text-align: center;
    transition: background 250ms ease-in-out, 
                transform 150ms ease;
    -webkit-appearance: none;
    -moz-appearance: none;
}

button:hover,
button:focus {
    background: #0053ba;
}

button:focus {
    outline: 1px solid #fff;
    outline-offset: -4px;
}

button:active {
    transform: scale(0.99);
}

.memory {
	background-color: rgb(70, 70, 70);
	border-radius: 15px;
}

.memory-row {
	display: flex;
	flex-direction: row;
}

.memory-item {
	width: 3em;
}

.mem-reg-container {
	display: flex;
	flex-direction: row;
	grid-gap: 2rem;
	gap: 2rem;
}

.registers {
	background-color: rgb(70, 70, 70);
	border-radius: 15px;
	padding: 1.5rem;
}

.output-box {
	background-color: rgb(70, 70, 70);
	border-radius: 15px;
	padding: 1.5rem;
}

.green-border {
	border: 1px solid green;
}
